import { useState, useRef, useEffect } from 'react';

function DrawingModal({ char, onSave, onClose, initialPath }) {
  const canvasRef = useRef();
  const [isDrawing, setIsDrawing] = useState(false);
  const [isErasing, setIsErasing] = useState(false);

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');
    ctx.lineWidth = 20;
    ctx.lineCap = 'round';
    ctx.strokeStyle = 'black';

    if (initialPath) {
      const img = new Image();
      img.src = initialPath;
      img.onload = () => {
        ctx.drawImage(img, 0, 0);
      };
    }
  }, [initialPath]);

  const getCanvasPoint = (clientX, clientY) => {
    const canvas = canvasRef.current;
    const rect = canvas.getBoundingClientRect();
    return {
      x: ((clientX - rect.left) / rect.width) * canvas.width,
      y: ((clientY - rect.top) / rect.height) * canvas.height,
    };
  };

  const handlePointerDown = (e) => {
    const { clientX, clientY } = e.touches ? e.touches[0] : e;
    const { x, y } = getCanvasPoint(clientX, clientY);
    setIsDrawing(true);
    const ctx = canvasRef.current.getContext('2d');
    ctx.beginPath();
    ctx.moveTo(x, y);
  };

  const handlePointerMove = (e) => {
    if (!isDrawing) return;
    const { clientX, clientY } = e.touches ? e.touches[0] : e;
    const { x, y } = getCanvasPoint(clientX, clientY);
    const ctx = canvasRef.current.getContext('2d');
    ctx.lineTo(x, y);
    ctx.stroke();
  };

  const handlePointerUp = () => {
    setIsDrawing(false);
  };

  const handleSave = () => {
    const canvas = canvasRef.current;
    const smallCanvas = document.createElement('canvas');
    const smallCtx = smallCanvas.getContext('2d');
    smallCanvas.width = 100;
    smallCanvas.height = 100;
    smallCtx.drawImage(canvas, 0, 0, 100, 100);
    const dataURL = smallCanvas.toDataURL();
    onSave(char, dataURL);
  };

  return (
    <div className="modal-overlay">
      <div className="modal">
        <h2>Draw {char}</h2>
        <canvas
          ref={canvasRef}
          width={1000}
          height={1000}
          onMouseDown={handlePointerDown}
          onMouseMove={handlePointerMove}
          onMouseUp={handlePointerUp}
          onMouseLeave={handlePointerUp}
          onTouchStart={handlePointerDown}
          onTouchMove={handlePointerMove}
          onTouchEnd={handlePointerUp}
          style={{ touchAction: 'none', width: '500px', height: '500px', border: '1px solid #000' }}
        />
        <button onClick={handleSave}>Save</button>
        <button onClick={onClose}>Close</button>
        <button onClick={() => setIsErasing(!isErasing)}>{isErasing ? 'Stop Erasing' : 'Erase'}</button>
      </div>
    </div>
  );
}

export default DrawingModal;