import { useState } from 'react';
import DrawingModal from './DrawingModal';
import * as opentype from 'opentype.js';

const letters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789!@#$%^&*()'.split('');

function App() {
  const [selectedLetter, setSelectedLetter] = useState(null);
  const [letterPaths, setLetterPaths] = useState({});

  const saveLetter = (char, path) => {
    setLetterPaths(prev => ({ ...prev, [char]: path }));
    setSelectedLetter(null);
  };

  const generateFont = () => {
    const glyphs = letters.map(char => {
      const pathData = letterPaths[char];
      if (!pathData) return null;

      const img = new Image();
      img.src = pathData;
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      canvas.width = 1000;
      canvas.height = 1000;
      ctx.drawImage(img, 0, 0, 1000, 1000);

      const path = new opentype.Path();
      const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
      const { data, width, height } = imageData;

      for (let y = 0; y < height; y++) {
        for (let x = 0; x < width; x++) {
          const index = (y * width + x) * 4;
          const alpha = data[index + 3];
          if (alpha > 128) {
            path.moveTo(x, y);
            path.lineTo(x + 1, y);
            path.lineTo(x + 1, y + 1);
            path.lineTo(x, y + 1);
            path.closePath();
          }
        }
      }

      return new opentype.Glyph({
        name: char,
        unicode: char.charCodeAt(0),
        advanceWidth: 1000,
        path: path
      });
    }).filter(glyph => glyph !== null);

    const font = new opentype.Font({
      familyName: 'MyCustomFont',
      styleName: 'Regular',
      unitsPerEm: 1000,
      ascender: 800,
      descender: -200,
      glyphs: glyphs
    });

    font.download();
  };

  return (
    <div className="App">
      <div className="grid">
        {letters.map(char => (
          <button
            key={char}
            className="letter-square"
            onClick={() => setSelectedLetter(char)}
          >
            {char}
            {letterPaths[char] && <img src={letterPaths[char]} alt={char} style={{ width: '80%', height: '80%' }} />}
          </button>
        ))}
      </div>
      <button onClick={generateFont}>Save Font</button>
      {selectedLetter && (
        <DrawingModal
          char={selectedLetter}
          onSave={saveLetter}
          onClose={() => setSelectedLetter(null)}
          initialPath={letterPaths[selectedLetter]}
        />
      )}
    </div>
  );
}

export default App;